<template>
      <div class="news">
          <div class="news_title">
              {{data.title}}
            <div class="news_time">
                {{data.publish}}
            </div>
          </div>
          <div class="news_content" v-html="data.content">
          </div>
  </div>
</template>

<script>
  export default {
    name: 'news',
    data() {
      return {
          id: this.$route.params.newsId|| '',
          data: {}

      }
    },
    created() {
        this.getData(); 
    },
    methods: {
        getData() {
            this.axios.get('/app/news/newsDetail?id='+this.id).then(res => {
                if (res.s == 1) { 
                    this.data = res.d;
                    
                } 
          }) 
        }
    }
  }
</script>

<style lang="scss" scoped>
@function rem($px) {
    @return $px / 50px * 1rem;
}
  .news {
      background: #fff;
      .news_title {
          padding: rem(15px) rem(15px);
          font-size: rem(17px);
          border-bottom: rem(1px) solid #eee;
          color: #333;
      }
      .news_time {
          padding-top: rem(4px);
          font-size: rem(12px);
          color: #A8A8A8;
      }
      .news_content {
          padding: rem(15px)  rem(15px);
          line-height: rem(26px);
      }

  }
  
</style>

